@use '../scss/media-queries.scss' as *;

.movie-preview {
  &__card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 100%;

    @include gt-sm {
      width: 49%;
    }

    &:hover {
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    }
  }

  &__image{
    width: 100%;
  }

  &__container {
    padding: 2px 16px;
  }
}
